import React from 'react';
import s from './SingleRoom.module.css';

const SingleRoom = ({
  name, fullness, number, type,
}) => (
  <div className={s.container}>
    <div className={s.img_container}>
      <img src="/room.jpeg" alt="" className={s.img} />
      {fullness === number
        ? <span className={`${s.status} ${s.taken}`}></span>
        : <span className={`${s.status} ${s.available}`}></span>
      }
      <span className={s.room_name}>{name}</span>
    </div>
    <div className={s.row_container}>
      <span></span>
      <span>{fullness}</span>
    </div>
    <div className={s.row_container}>
      <span> </span>
      <span>{number}</span>
    </div>
    <div className={s.row_container}>
      <span></span>
      <span>{type}</span>
    </div>
  </div>
);

export default SingleRoom;
